<template>
  <div>
    <el-input v-model="path" readonly>
      <template #prepend> {{ label }} </template>
      <template #append>
        <el-button @click="copy" title="">
          <el-icon><CopyDocument /></el-icon>
          <!--  -->
        </el-button>
      </template>
    </el-input>

    <!-- <h3></h3> -->

    <MyExeList @action="handleAction" />
  </div>
</template>

<script setup>
import { CopyDocument } from "@element-plus/icons-vue";
</script>

<script>
import MyExeList from "./open-with-exe-list.vue";
import { ElMessage } from "element-plus";

export default {
  name: "open-with-panel",

  components: { MyExeList },

  computed: {
    current() {
      let cur = this.$store.getters["intent/current"];
      if (cur == null) {
        cur = {};
      }
      return cur;
    },

    revision() {
      return this.$store.getters["intent/revision"];
    },
  },

  data() {
    return {
      path: "",
      label: "",
      intent: {},
    };
  },

  methods: {
    handleAction(e) {
      let name = e.name;
      if (name == "intent") {
        this.handleIntent(e.intent);
      }
    },

    handleIntent(intent) {
      if (intent == null) {
        return;
      }
      let src1 = intent;
      let src2 = this.intent;
      let dst = {};
      this.copyfields(src1, dst);
      this.copyfields(src2, dst);
      let vo = { intents: [dst] };
      this.$store.dispatch("intent/run", vo);
    },

    copyfields(src, dst) {
      for (var k in src) {
        dst[k] = src[k];
      }
    },

    reload() {
      let intent = this.current;
      this.tryLoadFolder(intent.folder);
      this.tryLoadProject(intent.project);
      this.tryLoadRepository(intent.repository);
      this.tryLoadSubmodule(intent.submodule);
      this.tryLoadWorktree(intent.worktree);
    },

    tryLoadFolder(item) {
      if (item == null) {
        return;
      }
      this.label = "";
      this.path = item.path;
      this.intent = { folder: item };
    },

    tryLoadProject(item) {
      if (item == null) {
        return;
      }
      this.label = "";
      this.path = item.path;
      this.intent = { project: item };
    },

    tryLoadRepository(item) {
      if (item == null) {
        return;
      }
      this.label = "";
      this.path = item.path;
      this.intent = { repository: item };
    },

    tryLoadSubmodule(item) {
      if (item == null) {
        return;
      }
      this.label = "";
      this.path = item.path;
      this.intent = { submodule: item };
    },

    tryLoadWorktree(item) {
      if (item == null) {
        return;
      }
      this.label = "";
      this.path = item.path;
      this.intent = { worktree: item };
    },

    copy() {
      let text = this.path;
      this.$store.dispatch("clipboard/copy", text);
    },
  },

  mounted() {
    this.reload();
  },

  watch: {
    revision() {
      this.reload();
    },
  },
};
</script>
