<template>
  <div class="open-with-list">
    <div
      class="open-with-item"
      v-for="item in apps"
      :key="item.id"
      :title="item.title"
      @click="handleClickItem(item)"
    >
      <img class="icon" :src="item.icon" />
    </div>

    <!-- <div class="open-with-item" title="" @click="handleClickItemMore">     
